
import axios from "axios";
export default {
  name: "",
  props: {
    msg: String,
  },
  data() {
    return {
      response: [],
      errors: [],
      file: ''
    };
  },

  /*hook : personnalise le fonctionnement d'une application / d'un logiciel, en permettant de réaliser des actions en plus à des moments précis. 
  Mounted permet de récupérer automatiquement la liste 
  */
  mounted() {
    this.callRestService() //est appelé automatiquement une fois que le composant est chargé (i.e nous permet de récupérer la liste sans cliquer sur le bouton)
  },

  //partie des méthodes
  methods: {
    //récuperer la liste des images (équivalent à curl -X GET "http://localhost:8080/images en ligne de commande)
    callRestService() {
      axios
        .get(`images`)
        .then((response) => {
          // JSON responses are automatically parsed.
          this.response = response.data;
        })
        .catch((e) => {
          this.errors.push(e);
        });
    },
    //télécharger une image située sur le serveur et l'afficher côté client
    Download_Image() {
      var select = document.getElementById("select"); //on identifie les balises html ayant pour "id" = select
      var choice = select.selectedIndex; //numéro de l'index sélectionné dans le formulaire
      axios.get(`images/`+choice, { responseType:"blob" }) //blob : la réponse contient des données binaires, i.e des images stockées sous forme de bits.
        .then(function (response) {
          var reader = new window.FileReader(); //permet à une application web de lire le contenu du fichier
          reader.readAsDataURL(response.data); //permet de lire le contenu de l'objet Blob que l'on spécifie
          reader.onload = function() {  //se déclenche à la fin du processus de chargement du fichierf
          var imageDataUrl = reader.result; //retourne le contenu du fichier, i.e construit l'url du fichier
          document.getElementById("image").setAttribute("src",imageDataUrl); //l'attribut html ayant pour id "image" se voit doté d'une balise src ayant pour valeur 
          //l'url de l'image construite. C'est grâce à cette dernière étape que l'image est effectivement affichée. 
          }
      });
    },

    //uploader une image sur le serveur
    submitFile(){
        
        let formData = new FormData(); //on initialise le formulaire de soumission de photo
        formData.append('file', this.file); //ajoute le formulaire ayant pour id 'file' au document

            //construit la requête HTTP sur l'url /images (car on importe une image à cet endroit-là)
            axios.post(`images`,
                formData,
                {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
              }
            ).then(function(){
          console.log('Requete OK');
        })
        .catch(function(){
          console.log('Problème requête upload image');
        });
      },

      //met à jour le fichier lorsqu'on charge un nouveau fichier
    handleFileUpload(){
      this.file = this.$refs.file.files[0]; //quand le fichier a changé, on met à jour la variable "file" du fichier avec comme valeur le premier fichier trouvé dans la liste
    }
  }
  
};
