<!-------------------------------------------- Partie HTML --------------------------------------------> 
<!--

Hook : fonctions qui permettent de se "connecter" à la gestion d'état local et de cycle de vie de React (objet js / bibliothèque Js) depuis
des fonctions composants. Ils permettent d'utiliser react sans classe.

 -->

<template>
  <div class="hello">
    <h1>{{ msg }}</h1>

    <button class="Search__button" @click="callRestService()">Cliquez ici pour obtenir la liste d'images présentes sur le serveur</button>
    <!-- Lorsque l'on clique sur le bouton, cela appelle la méthode CallRestService() -->
  
    <div id= "v-for-dynamic" class = "selecteur">
      <h2> Listes d'images présentes sur le serveur :</h2>
      <select v-model="selected" id = "select">
        <option v-for="option in response" :key="option.id"> <!-- on parcourt toutes les lignes de la requêtes GET (située dans la variable response). -->
          <h3>{{ option }}</h3>    
        </option>
      </select>
    </div>
  <br><br>
  <!-- A noter la nécessité (depuis peu) d'attribuer une 'key' à chacun des élements dans le v-for -->

    <button class="Download_picture" @click="Download_Image()">Cliquez ici pour télécharger l'image sélectionnée</button>
      <br><br>
      <img id="image">
    

  <!-- L'idée ici est d'apeller la méthode Download_Image au clic du bouton. Une fois appelé, cette méthode se chargera de 'construire' l'url
  du fichier selectionné. A travers la balise <img> (et plus précisemmment son 'id'), nous allons réussir à afficher l'image souhaitée. -->

  <h2> Déposer une image sur le serveur</h2>

  <!-- Formulaire qui gère l'upload de nouvelles images sur le serveur -->
  <div class="formulaire">
    <label>File
      <input type="file" id="file" ref="file" v-on:change="handleFileUpload()"/> <!-- au changement de fichier d'importation on apelle cette méthode qui gère celui-ci -->
    </label>
    <button v-on:click="submitFile()">Submit</button>
  </div>

  <!-- Une fois que l'on clique sur le bouton on appelle la méthode submitFile qui va gérer la construction et l'import de la nouvelle image en
  respectant les contraintes déjà définies dans imageController (format d'image 'image/jpeg') -->
</div>
</template>

